import {Component, ComponentDidLoad, ComponentWillLoad, Element, h, Host, Prop, Watch} from '@stencil/core';
import {SelectHelper} from '../../services/SelectHelper';

@Component({
  tag: 'soul-option-group',
  styleUrl: 'soul-option-group.scss',
  shadow: true,
})
export class SoulOptionGroup implements ComponentDidLoad, ComponentWillLoad {

  @Prop({reflect: true}) label: string;
  @Prop({reflect: true}) disabled?: boolean;
  @Prop({reflect: true}) hidden?: boolean;

  @Element() hostElement: HTMLElement;

  private optionsElement: HTMLSlotElement;
  private mutationObserver: MutationObserver;

  componentWillLoad(): Promise<void> | void {
    this.mutationObserver = new MutationObserver(() => {
      const areOptionsFiltered = this.optionsElement.assignedElements().every(option => option.hasAttribute(SelectHelper.FILTER_ATTRIBUTE));

      if (areOptionsFiltered) {
        this.hostElement.setAttribute(SelectHelper.FILTER_ATTRIBUTE, 'true');
      } else {
        this.hostElement.removeAttribute(SelectHelper.FILTER_ATTRIBUTE);
      }
    });
  }

  componentDidLoad(): void {
    this.onDisabledChanged();
    this.onHiddenChanged();
  }

  @Watch('disabled')
  public onDisabledChanged(): void {
    this.optionsElement.assignedElements().forEach(option => {
      this.disabled ? (option as SoulOption).setAttribute(SelectHelper.GROUP_DISABLED_ATTRIBUTE, `${this.disabled}`) : (option as SoulOption).removeAttribute(SelectHelper.GROUP_DISABLED_ATTRIBUTE);
    });
  }

  @Watch('hidden')
  public onHiddenChanged(): void {
    this.optionsElement.assignedElements().forEach(option => {
      this.hidden ? (option as SoulOption).setAttribute(SelectHelper.GROUP_HIDDEN_ATTRIBUTE, `${this.hidden}`) : (option as SoulOption).removeAttribute(SelectHelper.GROUP_HIDDEN_ATTRIBUTE);
    });
  }

  render() {
    return (
      <Host>
        <div class="soul-option-group">
          <span class="soul-option-group__label soul-label soul-label--strong">{this.label}</span>
          <div class="soul-option-group__options">
            <slot ref={el => this.optionsElement = el as HTMLSlotElement} onSlotchange={this.onSlotChanged.bind(this)}></slot>
          </div>
        </div>
      </Host>
    );
  }

  private onSlotChanged(): void {
    this.mutationObserver.disconnect();
    this.optionsElement.assignedElements().forEach(option => {
      this.mutationObserver.observe(option, {attributes: true});
    });

  }

}

type SoulOption = HTMLSoulTextOptionElement | HTMLSoulAvatarOptionElement;
