@use "modules/core";
@use "modules/components/label";

:host {
  display: block;
}

:host([data-filtered]),
:host([hidden]:not([hidden="false"])) {
  display: none;
}

.soul-option-group {}

.soul-option-group__label {
  padding: core.$soul-space-squish-xs;
}


.soul-option-group__options {
  ::slotted(soul-text-option),
  ::slotted(soul-avatar-option) {
    padding: core.$soul-space-squish-2-xs;
  }
}

:host([disabled]:not([disabled="false"])) {
  .soul-option-group {
    opacity: core.$opacity-l;
    cursor: not-allowed;
  }
}
